import React from "react";
import "./BackButton.css";

const BackButton = () => (
    <a href="/editor" className="ml-3" title="Back to Editor"><i className="fas fa-arrow-circle-left text-left mt-3" id="back-arrow"></i></a>
)

export default BackButton;


